import {f7} from "framework7-react";
import {pageModel, PageModel} from "./mc/PageModel";
import {Event2} from "../../events/Event2";
import {viewTabs} from "./mainViewTabParams";

const {routes} = require("./routes");

const f7params = {
    routes:routes,
    pushState:true,
    // pushStateSeperator:"#!",
    // history:true,
    view:{
        pushState: true,
        pushStateSeparator:"",
        pushStateOnLoad:false,
        // history:true,
        passRouteParamsToRequest:true,
        mdSwipeBack:true,
        pushStateRoot:"/baobeidaola/website/index.html",
        browserHistory:true,
        browserHistorySeparator:"",
        // browserHistoryStoreHistory:false,
        browserHistoryOnLoad:true,
        browserHistoryAnimateOnLoad:true,
        browserHistoryAnimate:true,
        // browserHistoryRoot:"/"
        on:{
            pageInit:function (page){
                console.log("f7 params on page init", page);
                console.log("f7 params on page init2", page.router.currentRoute);
                pageModel.dispatchEvent(new Event2(PageModel.events.pageInit, page))
            },
            init:function (){
                console.log("f7 view on init ", f7.view.main.router.currentRoute);
                setTimeout(()=>{

                }, 1000);
            }
        },

    },
    on:{
        pageInit:function (page){
            console.log("f7 params on page init", page);
            // console.log("f7 params on page init2", page);
            pageModel.dispatchEvent(new Event2(PageModel.events.pageInit, page))
        },
        init:function (e){
            const router = f7.view.main.router;
            window.requestAnimationFrame(()=>{
                if(router.initialUrl === "/"){
                    f7.views.main.router.navigate({"path": viewTabs[0].router.path});
                }
                pageModel.dispatchEvent(new Event2(PageModel.events.f7Init));
            });
            // console.log("f7 params on f7 init", Object.keys(router), router.initialPages, router.initialUrl, f7.view.main.router);
        }
    },
    onPageInit:function (e, page){
        // console.log("f7 on page init", e, page, f7.view.main.router.history.join());
    },
    card: {
        // hideNavbarOnOpen: false,
    },


}
export {f7params}
